@import "reset/normalize.css";

@import "../../node_modules/swiper/css/swiper.min.css";

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: #000;
}

ul {
  list-style: none;
}

img {
  width: 100%;
  height: auto;
}

header {
  width: 100%;
  padding: 1.25rem 0;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1rem;
  overflow-x: hidden;
}

button {
  border-width: 0;
  background: transparent;
}

input::placeholder {
  font-weight: 400;
  font-size: 0.9rem;
  line-height: 1rem;
  color: #000;
  padding-left: 1.25rem;
}

@font-face {
  font-family: "Roboto";
  font-display: swap;
  font-style: normal;
  font-weight: 400;
  src: local("Roboto-Regular"), url("../fonts/Roboto-Regular.woff2") format("woff2");
}

@font-face {
  font-family: "Roboto";
  font-display: swap;
  font-style: normal;
  font-weight: 700;
  src: local("Roboto-Bold"), url("../fonts/Roboto-Bold.woff2") format("woff2");
}

.header {
  position: absolute;
  top: 0;
  z-index: 100;
}

.header-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-flow: row wrap;
}

.header-logo {
  font-weight: 700;
  font-size: 1.5rem;
  line-height: 1.8rem;
}

.header-logo__color-grey {
  color: #939393;
}

.header-list {
  display: none;
  flex: 100%;
}

.header-list.active {
  margin-top: calc(50vh - 280px);
  animation: fade-in 1s;
  background: #fff;
  min-height: 100vh;
  text-align: center;
}

.header-list__item {
  font-weight: 900;
  font-size: 2rem;
  line-height: 2.3rem;
  padding-top: 1.6rem;
}

.header-language {
  color: #939393;
  padding-top: 5.5rem;
}

.header-language__text {
  padding-bottom: 1.2rem;
}

.header-language__button {
  font-weight: 400;
  font-size: 1.1rem;
  line-height: 1.7rem;
  padding: 6.5px 5.5px;
  border-radius: 5px;
  background: #fff;
}

.header-language__button:not(:last-child) {
  margin-right: 0.9rem;
}

.header-language__button-active {
  background: #c4c4c4;
  color: #939393;
}

.header-sign {
  padding: 4.5rem 0 2.7rem;
  color: #939393;
  font-weight: 400;
  font-size: 0.9rem;
  line-height: 1.1rem;
  margin: 0 2rem;
}

@keyframes fade-in {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.burger-menu {
  display: block;
  position: relative;
  width: 21px;
  height: 24px;
}

.burger-menu span {
  position: absolute;
  background-color: #1a212f;
  left: 0;
  width: 100%;
  height: 2px;
  top: 9px;
}

.burger-menu:after {
  content: "";
  background-color: #1a212f;
  position: absolute;
  width: 100%;
  height: 2px;
  left: 0;
  bottom: 6px;
  transition: 0.3s;
}

.burger-menu:before {
  content: "";
  background-color: #1a212f;
  position: absolute;
  width: 100%;
  height: 2px;
  left: 0;
  top: 2px;
  transition: 0.3s;
}

.burger-menu.active span {
  transform: scale(0);
}

.burger-menu.active:after {
  transform: rotate(45deg);
  top: 8px;
  transition: all 0.3s ease 0s;
  background: #939393;
}

.burger-menu.active:before {
  transform: rotate(-45deg);
  top: 8px;
  transition: all 0.3s ease 0s;
  background: #939393;
}

